import { Grid, Slide, Typography, useMediaQuery } from "@mui/material";
import { Box, Container, useTheme } from "@mui/system";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DropdownSort from "../../../components/DropdownSort";
import PopoverSort from "../../../components/PopoverSort";
import {
  getOrderHistory,
  getPaymentHistory,
} from "../../../redux/reducers/profile";
import { noneResults } from "./Orders";

const Operations = ({ t }) => {
  const dispatch = useDispatch();
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.down(768));

  const { order_history } = useSelector(({ profile }) => profile);

  const [openPopover, setOpenPopover] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);

  const containerRef = useRef(null);

  useEffect(() => {
    dispatch(getOrderHistory(`?search=200`));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <section>
      <PopoverSort
        anchorEl={anchorEl}
        open={openPopover}
        setOpen={setOpenPopover}
      />
      <Container maxWidth="xl" sx={{ mb: 4 }}>
        {md && (
          <Typography variant="subtitle2" fontWeight="700" mt={3}>
            В ожидании
          </Typography>
        )}
        {!Array.isArray(order_history.results) ? (
          <Slide
            mt={2}
            direction="right"
            in={!Array.isArray(order_history.results)}
            container={containerRef.current}
          >
            {noneResults}
          </Slide>
        ) : (
          <>
            <DropdownSort
              setAnchorEl={setAnchorEl}
              // value={sortingBy}
              setOpen={setOpenPopover}
            />
            <Grid container spacing={{ xs: 2, md: 3 }}>
              {order_history?.results?.map((item, idx) => (
                <Grid item xs={12} lg={6}>
                  <Card t={t} item={item} key={idx} />
                </Grid>
              ))}
            </Grid>
          </>
        )}
      </Container>
    </section>
  );
};

export default Operations;

